body {
  margin: 0;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  /* colors: */
  --main-background-color: #364082;
  --secondary-background-color: #f4f4f4;
  --gray-background-color: #373434;

  /* fonts */
  --font-family-subtitle: 'Fira Sans', sans-serif;
  --font-family-title: 'Montserrat', sans-serif;
  --font-family-logo: 'Solway', serif;
  --font-color: #f3efe9;
  --secondary-font-color: #D08D57;
}

